// import react, react-markdown-editor-lite, and a markdown parser you like
import React from 'react'
import * as ReactDOM from 'react-dom'
import MarkdownIt from 'markdown-it'

// import style manually
import 'react-markdown-editor-lite/lib/index.css'
import ReactMarkdown from 'react-markdown'
import MdEditor, { Plugins } from 'react-markdown-editor-lite'

import saveToFile from '../utils/editor-plugin'

MdEditor.use(saveToFile, {
  start: 2,
})
const mdParser = new MarkdownIt(/* Markdown-it options */)
export default class Demo extends React.Component {
  static mdEditor = undefined
  constructor(props) {
    super(props)
    this.renderHTML = this.renderHTML.bind(this)

    this.state = {
      value: '# Hello',
    }
  }
  handleRenderHtml = text => mdParser.render(text)

  handleEditorChange = (it, event) => {
    // console.log('handleEditorChange', it.text, it.html, event);
    this.setState({
      value: it.text,
    })
  }

  handleImageUpload = async file => {
    const directoryHandle = `/Users/gimjinhyeong/bnt10_blog/my-blog/`
    const options = {
      id: 'foo',
      startIn: 'Users',
    }

    //Todo await    
    const _saveFile = async saveFile => {
      // console.log(saveFile)
      // return saveFile
      try {
        const dirHandle = await window.showDirectoryPicker(options)
        if (dirHandle) {
          return saveFile
        }
      } catch (e) {
        console.log(e)
      }
    }

    const imagehandle = new Promise(resolve => {
      const reader = new FileReader()
      console.log(reader.readyState)
      reader.onload = data => {
        resolve(data.target.result)
      }

      reader.readAsArrayBuffer(file)
    }).then(async res => {
      return await _saveFile(res)
    })

    return imagehandle
  }

  onCustomImageUpload = event => {
    console.log('onCustomImageUpload', event)
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      reader.onload = data => {
        resolve(data.target.result)
      }
      reader.readAsDataURL(file)
      console.log(reader)
      resolve({ url: 'result' })
    })
  }

  handleGetMdValue = () => {
    if (this.mdEditor) {
      alert(this.mdEditor.getMdValue())
    }
  }

  handleGetHtmlValue = () => {
    if (this.mdEditor) {
      alert(this.mdEditor.getHtmlValue())
    }
  }

  handleSetValue = () => {
    const text = window.prompt('Content')
    this.setState({
      value: text,
    })
  }

  renderHTML(text) {
    return React.createElement(ReactMarkdown, {
      source: text,
    })
  }

  render() {
    return (
      <div className="demo-wrap">
        <nav className="nav">
          <button onClick={this.handleGetMdValue}>getMdValue</button>
          <button onClick={this.handleGetHtmlValue}>getHtmlValue</button>
          <button onClick={this.handleSetValue}>setValue</button>
        </nav>
        <div className="editor-wrap" style={{ marginTop: '30px' }}>
          <MdEditor
            ref={node => (this.mdEditor = node || undefined)}
            style={{ height: '700px', width: '100%' }}
            renderHTML={this.handleRenderHtml}
            config={{
              view: {
                menu: true,
                md: true,
                html: true,
                fullScreen: true,
                hideMenu: true,
              },
              table: {
                maxRow: 5,
                maxCol: 6,
              },
              imageUrl: 'https://octodex.github.com/images/minion.png',
              syncScrollMode: ['leftFollowRight', 'rightFollowLeft'],
            }}
            onChange={this.handleEditorChange}
            onImageUpload={this.handleImageUpload}
            // onFocus={e => console.log('focus', e)}
            // onBlur={e => console.log('blur', e)}
            //onCustomImageUpload={this.onCustomImageUpload}
          />
        </div>
      </div>
    )
  }
}
